:root {
  --accent: #986B0D;
  --textColor: #312C93;
  --titleColor: #E30611;
  --journalism: Journalism, sans-serif;
  --mts: "Mts Sans";
}

@font-face {
  font-family: "Journalism";
  src: url("../fonts/journalism/journalism.eot");
  src: local("Journalism"), url("../fonts/journalism/journalism.eot?#iefix") format("embedded-opentype"), url("../fonts/journalism/journalism.woff2") format("woff2"), url("../fonts/journalism/journalism.woff") format("woff"), url("../fonts/journalism/journalism.ttf") format("truetype"), url("../fonts/journalism/journalism.otf") format("opentype"), url("../fonts/journalism/journalism.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Regular.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Regular.woff2") format("woff2"), url("../fonts/mts/MTSSans-Regular.woff") format("woff"), url("../fonts/mts/MTSSans-Regular.ttf") format("truetype"), url("../fonts/mts/MTSSans-Regular.otf") format("opentype"), url("../fonts/mts/MTSSans-Regular.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Medium.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Medium.woff2") format("woff2"), url("../fonts/mts/MTSSans-Medium.woff") format("woff"), url("../fonts/mts/MTSSans-Medium.ttf") format("truetype"), url("../fonts/mts/MTSSans-Medium.otf") format("opentype"), url("../fonts/mts/MTSSans-Medium.svg") format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Bold.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Bold.woff2") format("woff2"), url("../fonts/mts/MTSSans-Bold.woff") format("woff"), url("../fonts/mts/MTSSans-Bold.ttf") format("truetype"), url("../fonts/mts/MTSSans-Bold.otf") format("opentype"), url("../fonts/mts/MTSSans-Bold.svg") format("svg");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Black.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Black.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Black.woff2") format("woff2"), url("../fonts/mts/MTSSans-Black.woff") format("woff"), url("../fonts/mts/MTSSans-Black.ttf") format("truetype"), url("../fonts/mts/MTSSans-Black.otf") format("opentype"), url("../fonts/mts/MTSSans-Black.svg") format("svg");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFFDF7;
  font-family: var(--mts), "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--accent);
  overflow-x: hidden;
}
body.no-scroll {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  border-radius: 10px;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.container {
  width: 100%;
  max-width: 1330px;
  margin: 0 auto;
  padding: 0 25px;
}

@media screen and (max-width: 1279px) {
  .container {
    padding: 0 16px;
  }
}
.title {
  font-family: var(--journalism);
  font-weight: normal;
  font-size: 72px;
  line-height: 100%;
  color: var(--titleColor);
}
.title--small {
  font-size: 48px;
}

.close {
  position: absolute;
  width: 30px;
  height: 30px;
  padding: 2px;
}
.close:hover span, .close:focus span, .close:active span {
  background: var(--titleColor);
}
.close span {
  display: block;
  width: 100%;
  height: 3px;
  background: var(--textColor);
  border-radius: 6px;
  transition: background 0.3s ease;
}
.close span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}
.close span:nth-child(2) {
  transform: rotate(-45deg) translate(-4px, 4px);
}

.btn {
  min-width: 178px;
  min-height: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
  color: var(--textColor);
  background: url("../images/icons/btn-hover.svg");
  background: url("../images/icons/btn.svg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: background 0.2s ease, color 0.2s ease;
}
.btn:hover, .btn:focus {
  color: var(--titleColor);
  background-image: url("../images/icons/btn-hover.svg");
}

.star {
  position: absolute;
}
.star img {
  width: 100%;
}
.star__gold {
  display: none;
}
.star__blue {
  display: block;
}

@media screen and (max-width: 767px) {
  .title {
    font-size: 48px;
  }
  .title--small {
    font-size: 36px;
  }
}
@keyframes starsFlashing {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes itemRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes santa {
  0% {
    transform: rotate(8deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.header {
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
  z-index: 1000;
}
.header__left {
  position: absolute;
  top: 0;
  left: 0;
}
.header__right {
  position: absolute;
  top: 0;
  right: 0;
}
.header__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 10;
}
.header__star-1 {
  top: 35%;
  left: 22%;
}
.header__star-2 {
  top: 17%;
  right: 20%;
}
.header__burger {
  display: none;
  width: 40px;
  height: 40px;
  position: relative;
}
.header__burger-icon--hover {
  position: absolute;
  left: 0;
  top: 0;
}
.header__nav {
  margin-bottom: 80px;
}
.header__list {
  display: flex;
  justify-content: center;
  grid-gap: 30px;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  color: var(--textColor);
}
.header__link--active {
  color: var(--titleColor);
}
.header__mts {
  margin-bottom: 50px;
  display: inline-block;
}
.header__logo {
  margin-bottom: 25px;
  margin-left: 42px;
}
.header__text {
  width: 100%;
  max-width: 700px;
  font-weight: 500;
  font-size: 18px;
  line-height: 179.69%;
  text-align: center;
}
.header--mts {
  margin-bottom: 100px;
}
.header--mts .header__star-1 {
  left: auto;
  top: 100px;
  right: 25%;
}
.header__toy {
  position: absolute;
  right: 0;
  top: 0;
}
.header__menu {
  display: flex;
  align-items: center;
  align-self: flex-start;
}
.header__menu .header__mts {
  width: 143px;
  margin-bottom: 0;
  margin-right: 83px;
}
.header__menu .header__nav {
  margin-bottom: 0;
}
.header--mood {
  margin-bottom: 42px;
}

@media screen and (max-width: 1800px) {
  .header__left {
    width: 500px;
  }
  .header__right {
    width: 500px;
  }
  .header__toy {
    width: 400px;
  }
}
@media screen and (max-width: 1600px) {
  .header__left {
    width: 400px;
  }
  .header__right {
    width: 400px;
  }
  .header__toy {
    width: 300px;
  }
}
@media screen and (max-width: 1279px) {
  .header__left {
    width: 250px;
  }
  .header__right {
    width: 250px;
  }
  .header__mts {
    max-width: 150px;
    margin-bottom: 30px;
  }
  .header__logo {
    margin-left: 0;
    max-width: 300px;
  }
}
@media screen and (max-width: 1100px) {
  .header__left {
    width: 150px;
  }
  .header__right {
    width: 150px;
  }
  .header__toy {
    display: none;
  }
  .header__menu .header__mts {
    margin-right: 50px;
  }
  .header__burger {
    display: block;
    padding: 10px 6px;
    margin: 0 120px 73px auto;
    z-index: 100;
    transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    position: relative;
  }
  .header__burger span {
    height: 4px;
    background: var(--textColor);
    border-radius: 10px;
    position: absolute;
    left: 6px;
    right: 6px;
    transition: 0.25s ease-in-out;
  }
  .header__burger span:nth-child(1) {
    top: 10px;
  }
  .header__burger span:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  .header__burger span:nth-child(3) {
    bottom: 10px;
  }
  .header__burger:hover span, .header__burger:focus span, .header__burger:active span {
    background: var(--titleColor);
  }
  .header__burger.header__burger--open span:nth-child(1) {
    top: 22px;
    transform: rotate(135deg);
  }
  .header__burger.header__burger--open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  .header__burger.header__burger--open span:nth-child(3) {
    top: 22px;
    transform: rotate(-135deg);
  }
  .header__nav {
    margin-bottom: 0;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 90;
    background: #fffdf7;
    display: none;
  }
  .header__nav--active {
    display: block;
  }
  .header__list {
    padding: 16px;
    padding-top: 80px;
    flex-direction: column;
    grid-gap: 20px;
  }
  .header--mts, .header--mood {
    margin-bottom: 50px;
  }
  .header--mts .header__burger, .header--mood .header__burger {
    margin: 0;
    margin-left: auto;
  }
  .header__menu .header__mts {
    position: absolute;
    top: 0;
    left: 16px;
  }
}
@media screen and (max-width: 767px) {
  .header {
    padding: 10px 0 80px;
  }
  .header--mood {
    padding-bottom: 30px;
  }
  .header--mts {
    padding-bottom: 0;
  }
  .header__mts {
    width: 118px;
  }
  .header__logo {
    max-width: 248px;
    margin-bottom: 36px;
  }
  .header__star {
    width: 42px;
  }
  .header__text {
    font-size: 16px;
    line-height: 150%;
  }
}
@media screen and (max-width: 500px) {
  .header__left {
    width: 235px;
  }
  .header__right {
    width: 86px;
    top: 60px;
  }
  .header__burger {
    margin-right: 0;
  }
}
@media screen and (max-width: 460px) {
  .header__star-1 {
    display: none;
  }
  .header__star-2 {
    top: 11%;
  }
}
.main {
  flex-grow: 1;
  overflow-x: hidden;
}
.main--visible {
  overflow: visible;
}

.clock {
  margin-bottom: 186px;
}
.clock__top {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: relative;
}
.clock__star-big {
  top: 8%;
  left: 48%;
}
.clock__star-small {
  right: 30%;
  top: 30%;
}
.clock__star-down {
  bottom: -60px;
  right: -5%;
}
.clock__time {
  min-width: 590px;
  width: 590px;
  height: 590px;
  position: relative;
}
.clock__days {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-weight: normal;
  font-size: 116px;
  line-height: 100%;
  text-align: center;
  color: var(--titleColor);
  white-space: nowrap;
  font-family: var(--journalism);
}
.clock__info {
  width: 100%;
  max-width: 530px;
  margin-bottom: 40px;
}
.clock__title {
  margin-bottom: 25px;
}
.clock__text {
  line-height: 180%;
}
.clock__wrapper {
  flex-wrap: wrap;
  grid-gap: 17px 20px;
}
.clock__list {
  position: relative;
}
.clock__list .swiper-button-next {
  display: none;
}
.clock__item {
  width: 305px;
  min-width: 305px;
  padding: 17px;
  font-family: var(--journalism);
  text-transform: uppercase;
  font-weight: normal;
  font-size: 96px;
  line-height: 100%;
  color: var(--accent);
  cursor: pointer;
  position: relative;
}
.clock__item--active {
  color: var(--titleColor);
}
.clock__item--active .clock__item-img {
  animation-name: itemRotate;
  animation-duration: 30s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.clock__item-img {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 5;
}
.clock__item-dots {
  width: 83%;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 6%;
}
.clock__item-day {
  position: absolute;
  z-index: 10;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

@media screen and (max-width: 1365px) {
  .clock__top {
    flex-direction: column;
    align-items: center;
  }
  .clock__star-small {
    right: 0;
  }
  .clock__star-big {
    left: 0;
  }
  .clock__star-down {
    right: 10%;
  }
  .clock__time {
    margin-bottom: 50px;
  }
  .clock__item {
    width: calc(33.33% - 14px);
