@keyframes starsFlashing
   0%
      opacity: 1
   50%
      opacity: 0
   100%
      opacity: 1

@keyframes itemRotate
   0%
      transform: rotate(0deg)
   100%
      transform: rotate(360deg)

@keyframes santa
   0%
      transform: rotate(8deg)
   100%
      transform: rotate(0deg)
      